function getScriptURL() {
  let r: null | HTMLElement = null;
  $("script#inserted-body-script").each(function (_: number, e: HTMLElement) {
    r = e;
  });
  return $("<a>").prop("href", $(r!).attr("src"));
}

function basePath(url: string) {
  var str = url.substr(url.lastIndexOf("/") + 1) + "$";
  return url.replace(new RegExp(str), "");
}

function isScriptExternal() {
  let url = getScriptURL();
  if (url && window.location.origin !== url!.prop("origin")) {
    // console.log("Script has been loaded from an external host!");
    return true;
  } else {
    // console.log("Script has been loaded from the same host as the Dashboard!");
    return false;
  }
}

// Detect where we loaded this JS file from
export const baseURL = (function () {
  if (isScriptExternal()) {
    const url = getScriptURL();
    return url.prop("origin") + basePath(url.prop("pathname"));
  } else {
    return window.location.origin + "/local/";
  }
})();

