export function updateMessage(
  type: string,
  message: string,
  sticky: boolean = false
) {
  $("#grid-message").hide();
  $("#grid-message").html(
    "<span class='message-" + type + "'>" + message + "</span>"
  );

  if (sticky) $("#grid-message").stop(true, true).fadeIn();
  else $("#grid-message").stop(true, true).fadeIn().delay(5000).fadeOut();
}

export function updateScroll(source: string, target: string) {
//  console.log("updating " + target + " with scroll data from " + source);
  let scrollTop: number = $(source).scrollTop()!;
  let scrollLeft: number = $(source).scrollLeft()!;
  $(target).scrollTop(scrollTop).scrollLeft(scrollLeft);
}
