export function smartlyClassSetter(tiles: JQuery<Node>, init: boolean = false) {
  let removeStates: Array<string> = [
    "tile-primary",
    "overflow-hidden",
    "h-full",
    "material-icons",
    "flex-grow-0",
    "w-full",
    "self-start",
  ];

  tiles.each(function () {
    let tileClasses: Array<string> = $(this).attr("class")!.split(" ");
    let classes: string;
    let states: Array<string>;
    let state: string[];
    let stateValue: number;
    let target: JQuery<HTMLElement>;
    let touched: boolean = $(this).attr("state") !== undefined; // is already being watched

    switch (
      tileClasses[1] // the tile type class appears to always be second
    ) {
      case "temperature":
        target = $(this).find(".tile-primary");
        stateValue = parseInt(target?.contents().text() ?? "", 10);

        if (touched) {
          // updating existing tile
          $(this).removeClass("temp-" + $(this).attr("state"));
        }

        $(this).addClass("temp-" + stateValue);
        $(this).attr("state", stateValue);
        break;

      case "thermostat": // very fragile, dependent on html hierarchy
        target = $(this).find("div").children().first();
        classes = target.attr("class") ?? "";
        states = classes.split(" ");

        state = states.filter(function (val) {
          return removeStates.indexOf(val) == -1 ? true : false;
        });

        if (touched) {
          // updating existing tile
          $(this).removeClass($(this).attr("state"));
        }

        $(this).addClass(state.reverse()[0]);
        $(this).attr("state", state.reverse()[0]);

        break;

      case "bulb-color":
        target = $(this).find(".tile-primary>div>i");
        classes = target.attr("class") ?? "";
        states = classes.split(" ");

        state = states.filter(function (val) {
          return removeStates.indexOf(val) == -1 ? true : false;
        });

        if (touched) {
          // updating existing tile
          $(this).removeClass($(this).attr("state"));
        }

        $(this).addClass(state[0].replace("he-bulb_", ""));
        $(this).attr("state", state[0].replace("he-bulb_", ""));

        break;

      default:
        //console.log(tileClasses[1], "DEFAULT");
        //console.log($(this).find( '.tile-primary' ).attr('class'), "TEST");
        target = $(this).find(".tile-primary");
        classes = target.attr("class") ?? "";
        states = classes.split(" ");

        state = states.filter(function (val) {
          return removeStates.indexOf(val) == -1 ? true : false;
        });

        if (touched) {
          // updating existing tile
          $(this).removeClass($(this).attr("state"));
        }

        $(this).addClass(state[0]);
        $(this).attr("state", state[0]);
    }

    target.addClass("state-target"); // make it easier to target
  });

  if (init) {
    smartlyClassMonitor("#app");
  }
}

function smartlyClassMonitor(id: string) {
  const observer = new MutationObserver(function (mutations) {
    mutations.forEach(function (mutation) {
      let tileRoot: JQuery<Node>;

      switch (mutation.type) {
        case "characterData":
          tileRoot = $(mutation.target).closest(".tile"); // only populating if it's a match
          smartlyClassSetter(tileRoot);

          break;

        default:
          // determine if changed element is the tile root div
          let oldState: any = $(mutation.target).attr("state") ?? null;
          if (oldState) {
            return;
          } // we don't want to root tile div

          let targetClasses: string = $(mutation.target).attr("class")!;
          if (
            targetClasses &&
            (targetClasses.includes("state-target") ||
              targetClasses.includes("tile-primary"))
          ) {
            // @TODO: find out why state-target class isn't being injected
            tileRoot = $(mutation.target).closest(".tile"); // only populating if it's a match
            smartlyClassSetter(tileRoot);
          }
      }
    });
  });

  // initialize the observer on passed id
  observer.observe($(id)[0], {
    childList: true,
    attributes: true,
    subtree: true,
    characterData: true,
  });
}
