import MicroModal from "micromodal";
import { GridStack } from "gridstack/dist/gridstack";
import { getLayout } from "./fetchers";
import { updateMessage, updateScroll } from "./dom-helpers";

interface WithOnAvailable {
  onAvailable: any;
}

interface JQueryWithOnAvailable extends WithOnAvailable, JQuery<HTMLElement> {}

// DON'T call this directly, use the wrappers below!
function handleJson(
  operation: string,
  returnCallback: null | ((r: string | number | string[]) => void),
  newJson: string
) {
  ($.fn as JQueryWithOnAvailable).onAvailable = function (
    selector: any,
    fn: { call: (arg0: JQuery<any>) => void }
  ) {
    let timer: NodeJS.Timeout | number;
    if (this.length > 0) {
      fn.call(this);
    } else {
      timer = setInterval(function () {
        if ($(selector).length > 0) {
          fn.call($(selector));
          clearInterval(timer as NodeJS.Timeout);
        }
      }, 50);
    }
  };

  // $("head style")
  //   .last()
  //   .after(
  //     '<style id="popup-container" type="text/css">.popup-container {opacity: .1;}</style>'
  //   );
  // $("i:contains(settings)").css("background-color", "red");
  $("i:contains(settings)").one("click", function () {
    // console.log("on click 1");
    ($("div.navLink:contains(Advanced)") as JQueryWithOnAvailable).onAvailable(
      "div.navLink:contains(Advanced)",
      function () {
        // console.log("Advanced is now available");
        $("div.navLink:contains(Advanced)").one("click", function () {
          // console.log("on click 2");
          ($("textarea.exportBox") as JQueryWithOnAvailable).onAvailable(
            "textarea.exportBox",
            function () {
              // console.log("textarea.exportBox");
              const eb = $("textarea.exportBox");
              if (operation === "get") {
                let r = eb.val();
                $("div.navLink:contains(X)").trigger("click");
                $("#popup-container").remove();
                returnCallback!(r!);
              } else {
                // console.log("New JSON: ", newJson);
                eb.val(newJson);
                // To "commit" the new JSON this event needs to fire in the DOM:
                eb[0].dispatchEvent(new Event("input"));
                // Now "click" the save button
                $("div.inline-block:contains(Save Layout JSON)").trigger(
                  "click"
                );
                ($("div.importError") as JQueryWithOnAvailable).onAvailable(
                  "div.importError",
                  function () {
                    console.log("Done saving");
                    $("div.navLink:contains(X)").trigger("click");
                    $("#popup-container").remove();
                  }
                );
              }
            }
          );
        });
        $("div.navLink:contains(Advanced)").trigger("click");
      }
    );
  });
  $("i:contains(settings)").trigger("click");
}

// Function wrapper
function getJson(returnCallback: (r: string | number | string[]) => void) {
  handleJson("get", returnCallback, "");
}

// Function wrapper
function updateJson(jsonData: string) {
  handleJson("update", null, jsonData);
}

export function initGridstack(update: boolean = false) {
  const grid_tiles: {
    x: number;
    y: number;
    width: any;
    height: any;
    id: any;
    innerHTML: string;
  }[] = [];

  let grid_colums = 0;
  let grid_rows = 0;

  getLayout(function (response) {
    const inputJSON: {
      colWidth: number;
      rowHeight: number;
      gridGap: number;
      cols: number;
      rows: number;
      tiles: {
        id: number;
        template: string | null;
        templateExtra?: string | null;
        col: number;
        colSpan: number;
        row: number;
        rowSpan: number;
      }[];
      background: string;
    } = JSON.parse(response);

    grid_colums = inputJSON.cols;
    grid_rows = inputJSON.rows;

    for (const { index, tile } of inputJSON.tiles.map((tile, index) => ({
      index,
      tile,
    }))) {
      if (tile.template != "smartly" && tile.templateExtra != "javascript") {
        // extract innerHTML from each tile, except for our JSinject tile
        // make sure to target #app because there are 2 identical ids out there
        const tile_element = $("#app #tile-" + tile.id);

        // inject innerHTML for each tile
        grid_tiles.push({
          x: tile.col - 1,
          y: tile.row - 1,
          width: tile.colSpan,
          height: tile.rowSpan,
          id: tile.id,
          innerHTML: tile_element[0].outerHTML,
        });
        if (grid_colums < tile.col) grid_colums = tile.col;
        if (grid_rows < tile.row) grid_rows = tile.row;
      } else {
        // @TODO: need a way to prevent gridstack from rendering these without actually removing them
        //   delete inputJSON.tiles[index];
      }
    }

    const columnsUsed = grid_colums + 2;
    const rowsUsed = grid_rows + 2;
    const columnTotalWidth = inputJSON.colWidth + inputJSON.gridGap;
    const rowTotalHeight = inputJSON.rowHeight + inputJSON.gridGap;
    const stack_width = columnTotalWidth * columnsUsed; // ;
    const stack_height = rowTotalHeight * rowsUsed; // ;
    console.log({
      width: stack_width + "px",
      height: stack_height + "px",
      left: inputJSON.gridGap + "px",
    });

    // Build the fixed-width CSS
    console.log("columnTotalWidth", columnTotalWidth);
    console.log("inputJSON.colWidth", inputJSON.colWidth);
    console.log("rowTotalHeight", rowTotalHeight);
    console.log("rowsUsed", rowsUsed);
    console.log("stack_width", stack_width);
    console.log("stack_height", stack_height);
    const cssStyles: string[] = [];
    for (let x = 0; x < columnsUsed; x++) {
      cssStyles.push(
        `.grid-stack>.grid-stack-item[data-gs-x='${x}'] { left: ${
          columnTotalWidth * x
        }px}`
      );
      let widthExtra = x > 0 ? x * inputJSON.gridGap : 0;
      cssStyles.push(
        `.grid-stack>.grid-stack-item[data-gs-width='${x + 1}'] { width: ${
          inputJSON.colWidth * (x + 1) + widthExtra - 1
        }px}`
      );
      /*        cssStyles.push(
        `.grid-stack>.grid-stack-item[data-gs-width='${
          x + 1
        }']>.grid-stack-item-content { width: ${
          inputJSON.colWidth * (x + 1) + widthExtra - 1
        }px}`
      );
      */
      cssStyles.push(
        `.grid-stack>.grid-stack-item[data-gs-width='${
          x + 1
        }']>.tile { width: ${inputJSON.colWidth * (x + 1) + widthExtra - 1}px}`
      );
    }
    for (let y = 0; y < rowsUsed; y++) {
      cssStyles.push(
        `.grid-stack>.grid-stack-item[data-gs-y='${y}'] { top: ${
          rowTotalHeight * y
        }px}`
      );
      let heightExtra = y > 0 ? y * inputJSON.gridGap : 0;
      cssStyles.push(
        `.grid-stack>.grid-stack-item[data-gs-height='${y + 1}'] { height: ${
          inputJSON.rowHeight * (y + 1) + heightExtra
        }px}`
      );
    }
    cssStyles.push(
      `.grid-stack { min-height: ${stack_height}px; /* height: ${stack_height}px !important; */ width: ${stack_width}px !important; }`
    );
    cssStyles.push(
      `.grid-stack-item { min-width: ${inputJSON.colWidth}px !important; }`
    );
    cssStyles.push(
      `.grid-stack-item { min-height: ${inputJSON.rowHeight}px !important; }`
    );

    // wipe current settings, to be recalculated and injected
    $("#grid-stack-fixed-width").remove();
    $('<style id="grid-stack-fixed-width">')
      .prop("type", "text/css")
      .html(cssStyles.join(" \n"))
      .prependTo("body");

    // console.log("stack_width: ", stack_width);
    // console.log("GridStack: ", GridStack);

    const grid: any = GridStack.init({
      float: true,
      cellHeight: inputJSON.rowHeight + inputJSON.gridGap,
      column: columnsUsed,
      disableOneColumnMode: true,
      margin: inputJSON.gridGap,
      // resizable: DDResizeOpt,
      alwaysShowResizeHandle: true,
      styleInHead: true,
      minRow: rowsUsed,
      //maxRow: rowsUsed,
    } as any);

    function log(type: any, items: any[]) {
      let str = "";
      items.forEach(function (item) {
        str += " (x,y)=" + item.x + "," + item.y;
      });
      // console.log(type + items.length + " items." + str);
    }

    // These 3 are probably being attempted to be redeclared on 'update'.  Do we care?
    const loadGrid = () => {
      grid.removeAll();
      var items = GridStack.Utils.sort(grid_tiles); //inputJSON.tiles); //serializedData);
      grid.batchUpdate();
      items.forEach(function (item: any) {
        grid.addWidget(
          '<div><div class="grid-stack-item-content">' +
            item.innerHTML +
            "</div></div>",
          item
        );
      });
      grid.commit();
      $("#grid-message").hide();
      //updateScroll(".dashboard .wrapper.z-0", "#grid-stack-scroller");
      // ^ done in the open modal onclick, better reliability
    };

    const saveGrid = () => {
      // console.log("saveGrid inputJSON.tiles: ", inputJSON.tiles);
      let maxCols = inputJSON.cols;
      let maxRows = inputJSON.rows;

      grid.engine.nodes.forEach(function (node: any, i: number) {
        const index = inputJSON.tiles.findIndex(
          (x) => +x.id === +node.id && x.template !== "smartly"
        );
        // console.log("saveGrid index: ", index);
        // Don't check if index is "truthy", that makes id 0 invalid!
        if (index != null && index != -1) {
          inputJSON.tiles[index]["colSpan"] = node.width;
          inputJSON.tiles[index]["rowSpan"] = node.height;
          inputJSON.tiles[index]["col"] = node.x + 1;
          inputJSON.tiles[index]["row"] = node.y + 1;
          if (node.x + 1 > maxCols) maxCols = node.x + 1;
          if (node.y + 1 > maxRows) maxRows = node.y + 1;

          // update global grid_tiles so 'refresh' won't revert to loadGrid era data
          const internal_index = grid_tiles.findIndex(
            (x) => +x.id === +node.id
          );
          grid_tiles[internal_index]["x"] = node.x;
          grid_tiles[internal_index]["y"] = node.y;
          grid_tiles[internal_index]["width"] = node.width;
          grid_tiles[internal_index]["height"] = node.height;
        }
      });

      inputJSON.cols = maxCols;
      inputJSON.rows = maxRows;

      updateJson(JSON.stringify(inputJSON, null, 2));
      updateMessage("success", "Changes saved!");
    };

    const clearGrid = () => {
      grid.removeAll();
    };

    if (!update) {
      // if initialization
      MicroModal.init({ debugMode: false });

      $("#saveGrid").click(saveGrid);
      $("#loadGrid").click(loadGrid);

      grid.on("added", function (_: any, items: any[]) {
        log("added ", items);
      });
      grid.on("removed", function (_: any, items: any[]) {
        log("removed ", items);
      });
      grid.on("change", function (_: any, items: any[]) {
        updateMessage("warning", "There are unsaved changes!", true);
        log("change ", items);
      });

      $("#grid-stack-extra-styling").remove();
      $('<style id="grid-stack-extra-styling">')
        .prop("type", "text/css")
        .html(
          ".grid-stack > .grid-stack-item > .ui-resizable-se { " +
            "right: 7.5px;" +
            "bottom: 7.5px;" +
            // "filter: brightness(0) invert(1);" +
            "}"
        )
        .prependTo("body");

      $("#clearGrid").click(clearGrid);
      $("i:contains(settings)").parent().parent().prepend($("#open-modal-btn"));
      $("#open-modal-btn a").click(() => {
        initGridstack(true);
        MicroModal.show("modal-1"); // [2]
        updateScroll(".dashboard .wrapper.z-0", "#grid-stack-scroller");

        $("#app .wrapper").hide();
        //$("#app .header").css("opacity", ".5");
        $("#app .header>.justify-end").hide();
        // $("#app .header").css("visibility", "hidden");
        $("#app .header").hide();
        $("head style")
          .last()
          .after(
            '<style id="popup-container-style" type="text/css">.popup-container {visibility: hidden;}</style>'
          );
        $("i:contains(settings)").parent().show();
      });
      $("#close-modal-btn").click(() => {
        $("#app .wrapper").show();
        updateScroll("#grid-stack-scroller", ".dashboard .wrapper.z-0");
        MicroModal.close("modal-1"); // [2]

        $("#app .header").show();
        $("#app .header>.justify-end").show();
        $("#popup-container-style").remove();
        // $("#grid-stack-extra-styling").remove();
        // $("#app .header").css("visibility", "visible");
      });
      $("#close-modal-btn").dblclick(function () {
        console.log("prevented a double-click!");
      });

      $(".hdi").removeClass("hdi"); // show the paw
    }

    loadGrid();
  }); // callback for lJ()
}
